<template>
  <div class="sign">
    <el-divider>{{$t('交易报文')}}</el-divider>
    <div>
      <el-input
        v-model="tx"
        placeholder="交易报文"
        type="textarea"
        :autosize="{ minRows: 5, maxRows: 20}"
      ></el-input>
    </div>
    <div style="margin-top: 15px;">
      <el-button type="primary" @click="sign">{{$t('签名')}}</el-button>
    </div>
    <div v-if="sig">
      <el-divider>{{$t('签名')}}</el-divider>
      <el-input
        disabled
        v-model="sig"
        placeholder="交易报文"
        type="textarea"
        :autosize="{ minRows: 1, maxRows: 20}"
      ></el-input>
      <div style="margin-top: 15px;">
        <el-button
          type="primary"
          v-clipboard:copysig="sig"
          v-clipboard:success="onCopy"
        >{{$t('复制签名')}}</el-button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Sign",
  data() {
    return {
      state: this.store.state,
      tx: "",
      sig: null
    };
  },
  methods: {
    async sign() {
      try {
        console.log("sign");
        this.store.loading();
        this.sig = await this.chainApi.signRawTx(this.tx, this.state.identity);
        this.$message.success("签名成功");
        this.store.stopLoading();
      } catch (err) {
        console.log(err);
        this.$message.error(err.message);
        this.store.stopLoading();
      }
    },
    onCopy(e) {
      this.$message.success({
        message: "复制成功",
        type: "success"
      });
    },
    copysig: function(e) {}
  }
};
</script>



