<template>
  <div id="app">
    <el-container>
      <el-header>
        <Header />
      </el-header>
      <el-main>
        <div class="text">
          <h1>{{$t('没有CPU无法交易怎么办？')}}</h1>
          <p>{{$t('在这里，你可以使用EOS来支付交易所需要的CPU')}}</p>
        </div>

        <div v-loading="state.loading" :element-loading-text="$t('网络传输中,请耐心等待')">
