  padding: 10px;
  margin-bottom: 30px;
}

/* .el-select .el-input {
  width: 130px;
} */
.input-with-select {
  width: 80px;
  // color: #ffffff;
  // background-color: #f44336;
}

.el-container {
  height: 100%;
}

.el-header {
  background-color: #f44336;
  color: #ffffff;
  text-align: center;
  line-height: 60px;
}

.el-footer {
  background-color: #f44336;
  color: #ffffff;
  text-align: center;
  line-height: 40px;
}

/* .el-tabs {
  text-align: center;
} */

/* .el-aside {
  background-color: #d3dce6;
  color: #333;
  text-align: center;
  line-height: 200px;
} */

.el-main {
