/* body > .el-container {
  margin-bottom: 40px;
}

.el-container:nth-child(5) .el-aside,
.el-container:nth-child(6) .el-aside {
  line-height: 260px;
}

.el-container:nth-child(7) .el-aside {
  line-height: 320px;
} */

.lang_box {
  line-height: 70px;
  width: 48px;
  height: 50px;
  box-sizing: border-box;
  position: relative;
}
.lang_box .lang_header {
