  width: 48px;
  background: #494949b4;
  border-radius: 5px;
}
.lang_box .lang_content .lang_item {
  width: 48px;
  height: 35px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.lang_box .lang_content .lang_item:hover {
  background: #ffffff73;
  border-radius: 5px;
}

.flexx {
  word-wrap: break-word;
  word-break: break-all;
  overflow: hidden; /*这个参数根据需要来绝对要不要*/
}
</style>



