<template>
  <div style="display: none;">{{state.tx}}</div>
</template>

<script>
export default {
  name: "Send",
  created() {},
  updated() {
    if (this.state.tx) {
      this.send(this.state.tx);
    }
  },
  data() {
    return {
      state: this.store.state
    };
  },
  methods: {
    async send(tx) {
      if (!this.state.identity.name) {
        this.$message.error(this.$t("请登陆"));
        return;
      }
      try {
        const txs = await this.chainApi.tx(
          this.state.identity,
          this.state.tx,
          this.state.fee
        );

        let { originTx, feeTx } = txs;
        // sign originTx
        const signedOriginTx = await this.signTx(originTx, "交易确认");
        // sign feeTx
        const signedFeeTx = await this.signTx(feeTx, "支付手续费");

        const res = await this.chainApi.exec(signedOriginTx, signedFeeTx);
        this.store.setTxid(res.transaction_id);
        this.$message({
          showClose: true,
          message: this.$t("交易成功"),
          type: "success"
        });
        setTimeout(this.getEosBalance, 2000);
        setTimeout(() => {
          this.chainApi.getAccount(this.state.identity.name).then(res => {
            this.store.setAccount(res);
          });
        }, 2000);
      } catch (err) {
        console.error(err);
        debugger;
        let errMsg;
        if (err.alert) {
          errMsg = err.alert;
          // this.errorMsg = err.error;
          this.store.setErrorMsg(err.error);
          try {
            const errJson = JSON.parse(err.error);
            errMsg = errJson.error.details[0].message.split(":")[1].trim();
          } catch (err) {
            console.error(err);
          }
        } else {
          errMsg = err.toString();
        }
        if (errMsg.match("交易可能失败")) {
          this.$message.error({
            message: this.$t(errMsg),
            duration: 0,
            showClose: true
          });
        } else {
          this.$message.error(this.$t(errMsg));
        }
      } finally {
        this.store.stopLoading();
      }
    },
    async signTx(tx, message) {
      const msg = this.$message.info({
        message: this.$t(message),
        duration: 0,
        center: true
      });
      let signedTx;
      try {
        if (!!window.imToken) {
          signedTx = await this.chainApi.signAndComfirm(
            tx,
            this.state.identity
          );
        } else {
          signedTx = await this.chainApi.sign(tx);
        }
      } catch (err) {
        throw "拒绝交易";
      } finally {
        msg.close();
      }

      const txSigs = signedTx.transaction.signatures;
      // console.log(txSigs);
      if (txSigs.length <= 1) {
        throw "拒绝交易";
      } else if (txSigs[1].toString().substr(0, 3) != "SIG") {
        throw "拒绝交易";
      } else {
        return signedTx;
      }
    }
  }
};
</script>



