<template>
  <div>
    <el-divider>{{$t('手续费')}}</el-divider>
    <el-radio-group v-model="state.fee" size="mini">
      <el-radio-button border label="1">{{$t('简单交易')}} 0.01 EOS</el-radio-button>
      <el-radio-button border label="2">{{$t('复杂交易')}} 0.05 EOS</el-radio-button>
    </el-radio-group>
    <div style="margin-top: 5px;" v-if="state.eosBalance">
      {{$t('余额')}}:
      <strong>{{state.eosBalance}} EOS</strong>
    </div>
  </div>
</template>

<script>
export default {
  name: "Fee",
  data() {
    return {
      state: this.store.state
    };
  },
  methods: {}
};
</script>



