<template>
  <div>
    <el-tabs v-model="subMenu">
      <!-- CPU/NET -->
      <el-tab-pane :label="$t('CPU/NET')" name="cpunet"></el-tab-pane>
      <el-tab-pane :label="$t('内存')" name="ram"></el-tab-pane>
      <!-- 糖果 -->
      <el-tab-pane :label="$t('糖果')" name="airgrab"></el-tab-pane>
      <el-tab-pane :label="$t('签名')" name="sign"></el-tab-pane>
      <router-view></router-view>
    </el-tabs>
  </div>
</template>

<script>
export default {
  name: "Common",
  data() {
    return {
      state: this.store.state,
      subMenu: this.store.state.subMenu
    };
  },
  watch: {
    subMenu(subMenu) {
      this.store.setMenu(subMenu);
      this.$router.push("/common/" + subMenu);
    }
  },
  methods: {}
};
</script>



