<template>
  <div>
    <el-row>
      <el-col :span="14">
        <div style="text-align: left;">
          <span style="font-size:large;">Easy Pay</span>
          <span style="font-size:small;margin-left:3px;">{{$t('EOS CPU 代付')}}</span>
        </div>
      </el-col>
      <el-col :span="3">
        <div class="lang_box">
          <div class="lang_header" @click="clickFlag">
            <img
              :src="'static/img/' + $i18n.locale + '.png'"
              style="height: 16px;"
              :alt="$i18n.locale"
            />
          </div>
        </div>
      </el-col>
      <el-col :span="state.identity.name?6:7">
        <div class="right">
          <div id="login">
            <div v-if="state.identity.name">{{state.identity.name}}</div>
            <div v-else>
              <el-button round @click="login">{{$t('登陆')}}</el-button>
            </div>
          </div>
        </div>
      </el-col>
      <el-col :span="state.identity.name?1:0">
        <div>
          <div id="logout">
            <div v-if="state.identity.name">
              <i class="el-icon-delete" @click="logout"></i>
            </div>
          </div>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  name: "Header",
  created() {
    // get lang from localStorage
    if (localStorage.getItem("language")) {
      this.$i18n.locale = localStorage.getItem("language");
    } else {
      localStorage.setItem("language", this.$i18n.locale);
    }
    for (const i in this.lang.all) {
      if (this.$i18n.locale == this.lang.all[i]) {
        this.lang.now = Number(i);
      }
    }
    // auto login
    setTimeout(this.login, 2000);
  },
  updated() {
    console.log("header updated");
    if (this.state.eosBalance == 0 && this.state.identity.name) {
      this.getEosBalance();
      this.getAccount();
    }
  },
  data() {
    return {
      lang: {
        now: 0,
        all: ["zh", "en"]
      },
      state: this.store.state
    };
  },
  methods: {
    login() {
      this.chainApi
        .connectIdentity()
        .then(res => {
          this.store.setIdentity(res);
          // this.getBalance();
          // this.getTokenList();
          // this.stake_data.receiver = res.name;
        })
        .catch(err => {
          this.$message.error(this.$t(err.toString()));
        });
    },
    logout() {
      // this.curToken = {
      //   symbol: "EOS",
      //   balance: null,
      //   contract: "eosio.token",
      //   precision: 4
      // };
      // this.eosBalance = null;
      this.chainApi.changeIdentity().then(() => {
        this.store.clearIdentity();
      });
    },
    clickFlag() {
      this.lang.now += 1;
      if (this.lang.now >= this.lang.all.length) {
        this.lang.now = 0;
      }
      this.$i18n.locale = this.lang.all[this.lang.now];
      localStorage.setItem("language", this.$i18n.locale);
    },
    async getEosBalance() {
      const account = this.state.identity.name;
      const contract = "eosio.token";
      const symbol = "EOS";
      try {
        const bal = await this.chainApi.getBalance(contract, account, symbol);
        this.store.setEosBalance(bal);
      } catch (err) {
        console.error(err);
        this.$message.error(this.$t("获取余额失败"));
      }
    },
    getAccount() {
      this.chainApi.getAccount(this.state.identity.name).then(res => {
        this.store.setAccount(res);
      });
    }
  }
};
</script>

<style scoped lang="less">
@rem: round(1/75 * 2, 6) * 1rem;

.right {
  text-align: right;
}

.box {
  border: 1px solid #ebebeb;
  border-radius: 5px;
  padding: 20px;
  margin: 10px;
}

.lang_box {
  line-height: 70px;
  width: 48px;
  height: 50px;
  box-sizing: border-box;
  position: relative;
}
.lang_box .lang_header {
  position: absolute;
  top: 15px;
  left: 0;
  width: 48px;
  height: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #ffffff73;
  border-radius: 10px;
}
.lang_box .lang_content {
  position: absolute;
  top: 45px;
  left: 0;
  width: 48px;
  background: #494949b4;
  border-radius: 5px;
}
.lang_box .lang_content .lang_item {
  width: 48px;
  height: 35px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.lang_box .lang_content .lang_item:hover {
  background: #ffffff73;
  border-radius: 5px;
}
</style>



