<template>
  <div>
    <el-tabs type="card" stretch v-model="func" @tab-click="changeFunc">
      <div v-if="state.account">
        <div>
          {{$t('我的内存')}}({{$t('Bytes')}}) :
          {{$t('已用')}}:{{state.account.ram_usage}}/{{$t('总量')}}:{{state.account.ram_quota}}
        </div>
      </div>
      <el-tab-pane :label="$t('购买')" name="buy">
        <div style="margin-top: 15px;">
          <el-input size="small" :placeholder="$t('EOS账号')" v-model="receiver">
            <template slot="prepend">{{$t('受益人')}}</template>
          </el-input>
        </div>
        <div style="margin-top: 15px;">
          <el-radio v-model="mode" label="EOS">{{$t('EOS')}}</el-radio>
          <el-radio v-model="mode" label="Bytes">{{$t('Bytes')}}</el-radio>
        </div>
        <div style="margin-top: 15px;">
          <el-input size="small" placeholder="0" v-model="buy_amount" type="number">
            <template slot="prepend">{{$t('购买')}} {{$t('内存')}}</template>
            <template slot="append">{{$t(mode)}}</template>
          </el-input>
        </div>
        <div
          v-if="buy_amount>0"
          style="margin-top: 5px;height:20px;text-align: right"
        >≈ {{buy_convert}} {{$t(mode=="EOS"?"Bytes":"EOS")}}</div>
        <div v-if="receiver&&buy_amount" style="margin-top: 15px;">
          <el-button type="primary" @click="send">{{$t('购买')}} {{$t('内存')}}</el-button>
        </div>
      </el-tab-pane>
      <el-tab-pane :label="$t('出售')" name="sell">
        <div style="margin-top: 15px;">
          <el-input
            size="small"
            placeholder="0"
            v-model="sell_amount"
            type="number"
            @input="checkSell"
          >
            <template slot="prepend">{{$t('出售')}} {{$t('内存')}}</template>
            <template slot="append">{{$t('Bytes')}}</template>
          </el-input>
        </div>
        <div
          v-if="sell_amount>0"
          style="margin-top: 5px;height:20px;text-align: right"
        >≈ {{sell_convert}} {{$t("EOS")}}</div>
        <div v-if="sell_amount" style="margin-top: 15px;">
          <el-button type="primary" @click="send">{{$t('出售')}} {{$t('内存')}}</el-button>
        </div>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
export default {
  name: "Ram",
  data() {
    return {
      state: this.store.state,
      func: "buy",
      receiver: this.store.state.identity.name,
      mode: "EOS",
      buy_amount: null,
      sell_amount: null,
      price: 0,
      usage: 0
    };
  },
  computed: {
    buy_convert: function() {
      return this.mode == "EOS"
        ? Number((this.buy_amount * this.price).toFixed(0)).toLocaleString(
            "en-US"
          )
        : (this.buy_amount / this.price).toFixed(4);
    },
    sell_convert: function() {
      return (this.sell_amount / this.price).toFixed(4);
    }
  },
  mounted() {
    this.store.loading();
    this.chainApi.getRamPrice().then(res => {
      this.store.stopLoading();
      this.price = res;
    });
    if (!this.state.account && this.state.identity.name) {
      this.chainApi.getAccount(this.state.identity.name).then(res => {
        this.store.setAccount(res);
        this.usage = Number(((res.ram_usage * 100) / res.ram_quota).toFixed(2));
      });
    } else {
      this.usage = Number(
        (
          (this.state.account.ram_usage * 100) /
          this.state.account.ram_quota
        ).toFixed(2)
      );
    }
  },
  methods: {
    checkSell() {
      // debugger;
      if (
        Number(this.sell_amount) >
        this.state.account.ram_quota - this.state.account.ram_usage
      ) {
        this.sell_amount =
          this.state.account.ram_quota - this.state.account.ram_usage;
      }
    },
    changeUnstake(item) {
      this.receiver = item.receiver;
      this.cpu_amount = item.cpu_amount;
      this.net_amount = item.net_amount;
    },
    getUnstakeOptions() {
      if (!this.state.identity.name) {
        this.$message.error(this.$t("请登陆"));
        return;
      }
      return this.chainApi.getUnstakeOptions(this.state.identity.name);
    },
    async changeFunc(tab, event) {
      this.receiver = "";
      this.buy_amount = this.sell_amount = null;
      if (tab.name == "buy") {
        this.receiver = this.state.identity.name;
      }
    },
    async send() {
      let tx;
      switch (this.func) {
        case "buy":
          this.buy_amount = Number(this.buy_amount);
          await this.chainApi.checkAccountExist(this.receiver);
          if (this.buy_amount <= 0) {
            throw "购买数量不能为0";
          }
          const action = {
            account: "eosio",
            data: {
              payer: this.state.identity.name,
              receiver: this.receiver
            }
          };
          if (this.mode == "EOS") {
            action.name = "buyram";
            action.data.quant = `${this.buy_amount.toFixed(4)} EOS`;
          } else {
            action.name = "buyrambytes";
            action.data.bytes = this.buy_amount;
          }
          tx = {
            actions: [action]
          };
          break;
        case "sell":
          this.sell_amount = Number(this.sell_amount);
          if (this.sell_amount <= 0) {
            throw "出售金额不能为0";
          }
          tx = {
            actions: [
              {
                account: "eosio",
                name: "sellram",
                data: {
                  account: this.state.identity.name,
                  bytes: this.sell_amount
                }
              }
            ]
          };
          break;
        default:
          throw "error function";
      }
      this.store.startTx(tx);
    }
  }
};
</script>



