<template>
  <div id="result">
    <div v-if="state.txid">
      <el-divider>{{$t('交易ID')}}</el-divider>
      <el-link type="success" :href="browserUrl" target="_blank">{{state.txid.substr(0,20)}}...</el-link>
    </div>
    <div v-if="state.errorMsg">
      <el-divider>{{$t('错误信息')}}</el-divider>
      <div class="flexx">
        <el-alert center :closable="false" type="error">{{state.errorMsg}}</el-alert>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "TxResult",
  created() {},
  updated() {
    if (this.state.txid || this.state.errorMsg) {
      document.getElementById("result").scrollIntoView(true);
    }
  },
  data() {
    return {
      state: this.store.state
    };
  },
  computed: {
    browserUrl: function() {
      return `https://bloks.io/transaction/${this.state.txid}`;
    }
  },
  methods: {}
};
</script>

<style scoped lang="less">
.flexx {
  word-wrap: break-word;
  word-break: break-all;
  overflow: hidden; /*这个参数根据需要来绝对要不要*/
}
</style>



