<template>
  <div>
    <el-tabs type="card" stretch v-model="func" @tab-click="clickCPU">
      <el-tab-pane :label="$t('抵押')" name="stake">
        <div>
          <el-input size="small" :placeholder="$t('EOS账号')" v-model="receiver" clearable>
            <template slot="prepend">{{$t('受益人')}}</template>
          </el-input>
        </div>
        <div style="margin-top: 15px;">
          <el-input size="small" placeholder="0" v-model="cpu_amount" type="number">
            <template slot="prepend">{{$t('抵押')}} CPU</template>
            <template slot="append">EOS</template>
          </el-input>
        </div>
        <div style="margin-top: 15px;">
          <el-input size="small" placeholder="0" v-model="net_amount" type="number">
            <template slot="prepend">{{$t('抵押')}} NET</template>
            <template slot="append">EOS</template>
          </el-input>
        </div>
        <div style="margin-top: 15px;" v-if="receiver!=state.identity.name">
          <el-radio v-model="transfer" label="0">{{$t('租借')}}</el-radio>
          <el-radio v-model="transfer" label="1">{{$t('赠送')}}</el-radio>
        </div>
        <div style="margin-top: 15px;" v-if="transfer=='1'">
          <el-alert type="warning" :closable="false">{{$t('抵押的EOS所属权将会转移给受益人')}}</el-alert>
        </div>
        <div v-if="receiver" style="margin-top: 15px;">
          <el-button type="primary" @click="send">{{$t('抵押')}}</el-button>
        </div>
      </el-tab-pane>
      <el-tab-pane :label="$t('解除抵押')" name="unstake">
        <div>
          <el-row>
            <el-col :span="16">
              <el-input size="small" :placeholder="$t('EOS账号')" v-model="receiver">
                <template slot="prepend">{{$t('解押自')}}</template>
              </el-input>
            </el-col>
            <el-col :span="8">
              <el-select size="small" @change="changeUnstake">
                <el-option
                  v-for="item in unstake_options"
                  :key="item"
                  :label="$t('请选择')"
                  :value="item"
                >{{item.receiver}}(CPU:{{item.cpu_amount}} EOS, NET:{{item.net_amount}} EOS)</el-option>
              </el-select>
            </el-col>
          </el-row>
        </div>
        <div style="margin-top: 15px;">
          <el-input size="small" placeholder="0" v-model="cpu_amount" type="number">
            <template slot="prepend">{{$t('解押')}} CPU</template>
            <template slot="append">EOS</template>
          </el-input>
        </div>
        <div style="margin-top: 15px;">
          <el-input size="small" placeholder="0" v-model="net_amount" type="number">
            <template slot="prepend">{{$t('解押')}} NET</template>
            <template slot="append">EOS</template>
          </el-input>
        </div>
        <div v-if="receiver" style="margin-top: 15px;">
          <el-button type="primary" @click="send">{{$t('解除抵押')}}</el-button>
        </div>
      </el-tab-pane>
      <el-tab-pane :label="$t('租赁')" name="rent">
        <div>
          <el-alert type="warning" :closable="true">{{$t('租赁资源付出租金换取更多资源的30天使用权')}}</el-alert>
        </div>
        <div style="margin-top: 15px;">
          1 EOS {{$t('可以租赁')}}
          <strong>{{rent_price.toFixed(0)}}</strong>
          EOS {{$t('对应资源30天的使用权')}}
        </div>
        <div style="margin-top: 15px;">
          <el-input size="small" :placeholder="$t('EOS账号')" v-model="receiver" clearable>
            <template slot="prepend">{{$t('受益人')}}</template>
          </el-input>
        </div>
        <div style="margin-top: 15px;">
          <el-input size="small" placeholder="0" v-model="cpu_amount" type="number">
            <template slot="prepend">{{$t('租赁')}} CPU</template>
            <template slot="append">EOS</template>
          </el-input>
        </div>
        <div
          v-if="cpu_amount>0"
          style="margin-top: 5px;height:20px;text-align: right"
        >≈ {{(cpu_amount*rent_price).toFixed(0)}} EOS CPU</div>
        <div style="margin-top: 15px;">
          <el-input size="small" placeholder="0" v-model="net_amount" type="number">
            <template slot="prepend">{{$t('租赁')}} NET</template>
            <template slot="append">EOS</template>
          </el-input>
        </div>
        <div
          v-if="net_amount>0"
          style="margin-top: 5px;height:20px;text-align: right"
        >≈ {{(net_amount*rent_price).toFixed(0)}} EOS NET</div>
        <div v-if="receiver" style="margin-top: 15px;">
          <el-button type="primary" @click="send">{{$t('租赁')}}</el-button>
        </div>
      </el-tab-pane>
      <el-tab-pane :label="$t('赎回')" name="refund">
        <div>{{$t('refund_info')}}</div>
        <div style="margin-top: 10px;">
          <el-button type="primary" @click="send">{{$t('赎回')}}</el-button>
        </div>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
export default {
  name: "CpuNet",
  data() {
    return {
      state: this.store.state,
      func: "stake",
      receiver: this.store.state.identity.name,
      cpu_amount: null,
      net_amount: null,
      transfer: "0",
      unstake_options: [],
      rent_price: 0
    };
  },
  methods: {
    changeUnstake(item) {
      this.receiver = item.receiver;
      this.cpu_amount = item.cpu_amount;
      this.net_amount = item.net_amount;
    },
    getUnstakeOptions() {
      if (!this.state.identity.name) {
        this.$message.error(this.$t("请登陆"));
        return;
      }
      return this.chainApi.getUnstakeOptions(this.state.identity.name);
    },
    async clickCPU(tab, event) {
      this.receiver = "";
      this.cpu_amount = this.net_amount = null;
      // this.stake_data = {};
      if (tab.name == "unstake") {
        this.unstake_options = [];
        const res = await this.getUnstakeOptions();
        for (const row of res.rows) {
          this.unstake_options.push({
            receiver: row.to,
            cpu_amount: Number(row.cpu_weight.split(" ")[0]),
            net_amount: Number(row.net_weight.split(" ")[0])
          });
        }
        // console.log(this.unstake_options);
      } else if (tab.name == "stake") {
        this.receiver = this.state.identity.name;
      } else if (tab.name == "rent") {
        this.receiver = this.state.identity.name;
        // get rent price
        this.rent_price = await this.chainApi.getRentPrice();
      }
    },
    async send() {
      this.cpu_amount = Number(this.cpu_amount);
      this.net_amount = Number(this.net_amount);
      let tx;
      switch (this.func) {
        case "stake":
          await this.chainApi.checkAccountExist(this.receiver);
          if (this.net_amount <= 0 && this.cpu_amount <= 0) {
            throw "金额不能为0";
          }
          tx = {
            actions: [
              {
                account: "eosio",
                name: "delegatebw",
                data: {
                  from: this.state.identity.name,
                  receiver: this.receiver,
                  stake_net_quantity: `${this.net_amount.toFixed(4)} EOS`,
                  stake_cpu_quantity: `${this.cpu_amount.toFixed(4)} EOS`,
                  transfer: this.transfer == "1" ? 1 : 0
                }
              }
            ]
          };
          break;
        case "unstake":
          await this.chainApi.checkAccountExist(this.receiver);
          if (this.net_amount <= 0 && this.cpu_amount <= 0) {
            throw "金额不能为0";
          }
          tx = {
            actions: [
              {
                account: "eosio",
                name: "undelegatebw",
                data: {
                  from: this.state.identity.name,
                  receiver: this.receiver,
                  unstake_net_quantity: `${this.net_amount.toFixed(4)} EOS`,
                  unstake_cpu_quantity: `${this.cpu_amount.toFixed(4)} EOS`
                }
              }
            ]
          };
          break;
        case "rent":
          await this.chainApi.checkAccountExist(this.receiver);
          if (this.net_amount <= 0 && this.cpu_amount <= 0) {
            throw "金额不能为0";
          }
          const actions = [
            {
              account: "eosio",
              name: "deposit",
              data: {
                owner: this.state.identity.name,
                amount: `${(this.cpu_amount + this.net_amount).toFixed(4)} EOS`
              }
            }
          ];
          if (this.cpu_amount > 0) {
            actions.push({
              account: "eosio",
              name: "rentcpu",
              data: {
                from: this.state.identity.name,
                receiver: this.receiver,
                loan_payment: `${this.cpu_amount.toFixed(4)} EOS`,
                loan_fund: "0.0000 EOS"
              }
            });
          }
          if (this.net_amount > 0) {
            actions.push({
              account: "eosio",
              name: "rentnet",
              data: {
                from: this.state.identity.name,
                receiver: this.receiver,
                loan_payment: `${this.net_amount.toFixed(4)} EOS`,
                loan_fund: "0.0000 EOS"
              }
            });
          }

          tx = { actions };
          break;
        case "refund":
          tx = {
            actions: [
              {
                account: "eosio",
                name: "refund",
                data: {
                  owner: this.state.identity.name
                }
              }
            ]
          };
          break;
        default:
          throw "error stake function";
      }
      this.store.startTx(tx);
    }
  }
};
</script>



