<template>
  <div>
    <!-- <div>
                  <el-alert center :closable="false" type="warning">{{$t('转账到交易所等公共地址请先小额尝试')}}</el-alert>
    </div>-->

    <div style="display: none;">{{state.identity.name}}</div>

    <div style="margin-top: 5px;">
      <el-input size="small" :placeholder="$t('EOS账号')" v-model="to" @blur="checkUser">
        <template slot="prepend">{{$t('接收方')}}</template>
      </el-input>
    </div>
    <div style="margin-top: 15px;">
      <el-row>
        <el-col :span="16">
          <el-input size="small" placeholder="0" v-model="amount" type="number">
            <template slot="prepend">{{$t('金额')}}</template>
          </el-input>
        </el-col>
        <el-col :span="8">
          <el-select
            v-model="curToken.symbol"
            size="small"
            filterable
            @change="changeToken"
            v-loading="gettingList"
          >
            <el-option-group
              v-for="group in tokenOptions"
              :key="group.label"
              :label="$t(group.label)"
            >
              <el-option
                v-for="item in group.options"
                :key="item.key"
                :label="item.symbol"
                :value="item"
              >{{item.symbol}} @{{item.contract}}</el-option>
            </el-option-group>
          </el-select>
        </el-col>
      </el-row>
    </div>
    <div v-if="curToken.balance>0" style="margin-top: 5px;height:20px;text-align: right">
      <img :src="curToken.logo" style="height:80%;" />
      | {{$t('余额')}}:
      <el-button type="text" @click="fill">{{curToken.balance}} {{curToken.symbol}}</el-button>
      @{{curToken.contract}}
    </div>
    <div style="margin-top: 15px;">
      <el-input size="small" placeholder="memo" v-model="memo">
        <template slot="prepend">{{$t('备注(选填)')}}</template>
      </el-input>
    </div>
    <div style="margin-top: 15px;" v-if="amount&&to">
      <el-button type="primary" @click="send">{{$t('转账')}}</el-button>
    </div>
  </div>
</template>

<script>
const exchange_list = [
  "binancecleos",
  "huobideposit",
  "addeoswallet",
  "bikicoineos5",
  "bitmaxaddrz1",
  "chainupsaas2",
  "cobowalletio",
  "mxcexdeposit",
  "newdexpocket",
  "dex.io",
  "dexeoswallet",
  "whaleextrust",
  "btexexchange",
  "eosiochaince",
  "findexfindex",
  "pwrdbyobolus",
  "yolonetworkx",
  "bidname.com",
  "onedex123451",
  "mdex1wallets",
  "bancorc11111",
  "bancorc11112",
  "bancorc11113",
  "bancorc11114",
  "bancorc11121",
  "bancorc11122",
  "bancorc11124",
  "bancorc11125",
  "bancorc11132",
  "bancorc11133",
  "bnt2eoscnvrt",
  "cointotheeos",
  "buttonbutton",
  "bancordexapi",
  "etbexchanger",
  "tokenstore.x",
  "wallet4bixin",
  "wwweosswapio",
  "bitfinexleo1",
  "newdexpublic",
  "eoslocallyio",
  "eostokensdex",
  "bkexeosusers",
  "timeforestcm",
  "bestwintoken",
  "lichanginput",
  "okbtothemoon",
  "deposit.pro"
];
export default {
  name: "Transfer",
  mounted() {
    this.getTokenList();
  },
  updated() {
    if (this.tokenOptions == 0) {
      this.getTokenList();
    }
  },
  data() {
    return {
      state: this.store.state,
      gettingList: false,
      curToken: {
        symbol: "EOS",
        balance: null,
        contract: "eosio.token",
        precision: 4,
        logo: "https://bloks.io/img/chains/eos.png"
      },
      to: "",
      amount: 0,
      memo: "",
      tokenOptions: []
    };
  },
  methods: {
    fill() {
      if (this.curToken.symbol == "EOS") {
        this.amount = Number(
          (
            this.curToken.balance - this.chainApi.getFee(this.state.fee)
          ).toFixed(4)
        );
        if (this.amount <= 0) {
          this.amount = 0;
        }
      } else {
        this.amount = this.curToken.balance;
      }
    },
    changeToken(token) {
      // console.log(token);
      this.amount = null;
      const { symbol, balance, contract, precision, logo } = token;
      this.curToken = { symbol, balance, contract, precision, logo };
      this.getBalance();
    },
    async checkUser() {
      if (this.to == this.state.identity.name) {
        this.$message.error(this.$t("不能转账给自己"));
        return;
      }
      if (this.to && this.to != "") {
        try {
          await this.chainApi.checkAccountExist(this.to);
        } catch (err) {
          this.$message.error(this.$t(err.toString()));
        }
      }
    },
    async getBalance() {
      this.store.loading();
      const account = this.state.identity.name;
      const { contract, symbol } = this.curToken;
      try {
        const bal = await this.chainApi.getBalance(contract, account, symbol);
        this.curToken.balance = bal;
        if (contract == "eosio.token" && symbol == "EOS") {
          this.store.setEosBalance(bal);
        }
      } catch (err) {
        console.error(err);
        this.$message.error(this.$t("获取余额失败"));
      } finally {
        this.store.stopLoading();
      }
    },
    async getTokenList() {
      const account = this.state.identity.name;
      if (!account) {
        return;
      }
      if (this.tokenOptions.length > 0) {
        return;
      }
      if (this.state.transfer.tokenOptions) {
        this.tokenOptions = Object.assign({}, this.state.transfer.tokenOptions);
        this.changeToken(this.curToken);
        return;
      }
      this.gettingList = true;
      const tokenList = await this.chainApi.getTokenList(account);
      // 排序
      const hot = [];
      hot.push({
        key: "eosio.token-eos-eos",
        symbol: "EOS",
        balance: 0,
        contract: "eosio.token",
        precision: 4,
        logo: "https://bloks.io/img/chains/eos.png"
      });
      const tokens = tokenList.concat();
      for (const token of tokens) {
        if (
          token.symbol == "LC" &&
          token.contract == "lichangtoken" &&
          token.balance > 0
        ) {
          hot.push(token);
        }
      }
      tokens.sort((a, b) => {
        return b.usd_value - a.usd_value;
      });
      this.tokenOptions = [
        {
          label: "常用",
          options: hot
        },
        {
          label: "代币",
          options: tokens
        }
      ];
      this.store.setTransferTokenOptions(this.tokenOptions);
      this.changeToken(this.curToken);
      this.gettingList = false;
    },
    async send() {
      try {
        if (this.to && this.amount) {
          if (exchange_list.includes(this.to) && this.memo == "") {
            // this.$message.error(this.$t("转账到交易所需要填写memo"));
            throw "转账到交易所需要填写memo";
          } else if (this.to == this.state.identity.name) {
            // this.$message.error(this.$t("不能转账给自己"));
            throw "不能转账给自己";
          } else {
            // check eos balance and fee
            const eosBalance = this.state.eosBalance;
            const balance = this.curToken.balance;
            const feeAmount = this.chainApi.getFee(this.state.fee);
            const amount = Number(this.amount);
            if (
              this.curToken.contract == "eosio.token" &&
              this.curToken.symbol == "EOS"
            ) {
              if (eosBalance < feeAmount + amount) {
                throw "EOS余额不够支付(转账金额+手续费)";
              }
            } else {
              if (eosBalance < feeAmount) {
                throw "余额不够支付手续费";
              }
              if (balance < amount) {
                throw "余额不足";
              }
            }
            const tx = {
              actions: [
                {
                  account: this.curToken.contract,
                  name: "transfer",
                  data: {
                    from: this.state.identity.name,
                    to: this.to,
                    quantity: `${amount.toFixed(this.curToken.precision)} ${
                      this.curToken.symbol
                    }`,
                    memo: this.memo
                  }
                }
              ]
            };
            this.store.startTx(tx);
          }
        } else {
          // this.$message.error(this.$t("输入内容不完整"));
          throw "输入内容不完整";
        }
      } catch (err) {
        this.$message.error(this.$t(err));
      }
    }
  }
};
</script>



