<template>
  <div class="airgrab">
    <div>
      <el-alert center :closable="false" type="info">{{$t('airgrab_info')}}</el-alert>
    </div>
    <el-divider>{{$t('选择糖果')}}</el-divider>
    <div>
      <el-select v-model="candy" filterable @change="changeCandy">
        <el-option-group v-for="group in candies" :key="group.label" :label="$t(group.label)">
          <el-option
            v-for="item in group.options"
            :key="item.key"
            :label="item.symbol"
            :value="item"
          >{{item.symbol}} @{{item.contract}}</el-option>
        </el-option-group>
      </el-select>
    </div>
    <el-divider>{{$t('糖果介绍')}}</el-divider>
    <div>{{$t(candy.description)}}</div>
    <el-divider>{{$t('官方网站')}}</el-divider>
    <div>
      <a :href="candy.website">{{candy.website}}</a>
    </div>
    <div style="margin-top: 15px;">
      <el-button type="primary" @click="send">{{$t('领取糖果')}}</el-button>
    </div>
  </div>
</template>

<script>
export default {
  name: "Airgrab",
  data() {
    return {
      state: this.store.state,
      candies: [
        {
          label: "热门",
          options: [
            {
              symbol: "BBT",
              contract: "blockbasetkn",
              action: "signup",
              data() {
                return {
                  owner: this.state.identity.name,
                  quantity: "0.0000 BBT"
                };
              },
              description: "BBT",
              website: "https://www.blockbase.network/"
            }
          ]
        },
        {
          label: "所有",
          options: []
        }
      ],
      candy: {
        symbol: "BBT",
        contract: "blockbasetkn",
        action: "signup",
        data(env) {
          debugger
          return {
            owner: env.state.identity.name,
            quantity: "0.0000 BBT"
          };
        },
        description: "BBT",
        website: "https://www.blockbase.network/"
      }
    };
  },
  methods: {
    changeCandy() {
      //todo
    },
    send() {
      try {
        const data = this.candy.data(this);
        const tx = {
          actions: [
            {
              account: this.candy.contract,
              name: this.candy.action,
              data
            }
          ]
        };
        this.store.startTx(tx);
      } catch (err) {
        this.$message.error(this.$t(err));
      }
    }
  }
};
</script>



