import VueRouter from 'vue-router';
import Transfer from "./views/Transfer";
import Contract from "./views/Contract";
import Common from "./views/Common";
import CpuNet from "./views/CpuNet";
import Airgrab from "./views/Airgrab";
import Ram from "./views/Ram.vue";
import Sign from "./views/Sign.vue";

const routes = [
    { path: '/', component: Transfer },
    { path: '/transfer', component: Transfer },
    { path: '/contract', component: Contract },
    {
        path: '/common', component: Common,
        children: [
            { path: '', component: CpuNet },
            { path: 'cpunet', component: CpuNet },
            { path: 'airgrab', component: Airgrab },
            { path: 'ram', component: Ram },
            { path: 'sign', component: Sign },
        ]
    }

]

// 3. 创建 router 实例，然后传 `routes` 配置
// 你还可以传别的配置参数, 不过先这么简单着吧。
const router = new VueRouter({
    routes // (缩写) 相当于 routes: routes
})

export default router


