export default {
    state: {
        menu: "transfer",
        subMenu: 'cpunet',
        account: null,
        identity: {},
        fee: 1,
        errorMsg: null,
        txid: null,
        tx: null,
        eosBalance: 0,
        loading: false,
        transfer: {
            tokenOptions: null
        }
    },
    setAccount(account) {
        this.state.account = account
    },
    setIdentity(identity) {
        this.state.identity = identity
    },
    clearIdentity() {
        this.state.dentity = {}
        this.state.eosBalance = 0
    },
    setFeeLevel(fee) {
        this.state.fee = fee
    },
    loading() {
        this.state.loading = true
    },
    stopLoading() {
        this.state.loading = false
    },
    startTx(tx) {
        this.state.loading = true
        this.state.errorMsg = null
        this.state.txid = null
        this.state.tx = Object.assign({}, tx)
    },
    setTxid(txid) {
        this.state.txid = txid
        this.state.tx = null
        this.state.loading = false
    },
    setEosBalance(bal) {
        this.state.eosBalance = bal
    },
    setErrorMsg(err) {
        this.state.errorMsg = err
        this.state.tx = null
        this.state.loading = false
    },
    setMenu(menu) {
        this.state.menu = menu
    },
    setSubMenu(subMenu) {
        this.state.subMenu = subMenu
    },
    setTransferTokenOptions(options) {
        this.state.transfer.tokenOptions = Object.assign({}, options)
    }

}


